<script>
import Address from '@/ui-mini/address/index.vue'
import 'element-plus/es/components/input-number/style/css'
import {ElInputNumber, checkboxEmits} from 'element-plus'  
import { ref,computed,reactive,onMounted ,nextTick,watch,isProxy,isRef, watchEffect, toRaw, onUpdated} from 'vue'
import _, { eq, forEach } from 'lodash'
import { create, all, and } from 'mathjs'
import SubmitButton from'@/components/submitButton'
import { anyTypeAnnotation, assertFunctionTypeAnnotation } from '@babel/types'



export default{
    name:'BuyCar',
    components:{Address,ElInputNumber,SubmitButton},
    setup(props,{emit}){
        let status = reactive({
            'item-choosed':[],
            'productList':[]
        })
        const mathConfig = { 
            number: 'BigNumber',
            precision: 20
        }

        const math = create(all, mathConfig);
        //shop input
        let nodeShop = ref()
        let nodeItem = ref()
        let rea_choosed_list = reactive([])


        //
        let com_productList = computed(() => { 
            let tmp =JSON.parse(localStorage.getItem('buyCarList'))
            let arr = []
            tmp.forEach((selfItem,selfIndex,Arr) => {
                if(tmp.length!==0){
                    arr.push(selfItem)
                    arr[selfIndex].item  = [arr[selfIndex].item]

                    tmp.forEach((secItem,secIndex,secArr)=>{
                        //,
                        if(selfIndex !==secIndex){
                            //
                            if(secItem.shopId ==selfItem.shopId){
                                //item
                                if(secItem.item[0].skuId== arr[selfIndex].item[0].skuId ){
                                    arr[selfIndex].num = arr[selfIndex].num  + secItem.num
                                    
                                }else{
                                    arr[selfIndex].item.push(secItem.item)
                                }
                            }

                        }
                    })
                }          
            })
            //
            let equalArr = []      
            for(let item of arr){
               let judge = equalArr.find((el) => { return el.shopId == item.shopId })
               if(!judge){
                equalArr.push(item)
               }

            }
            return equalArr

        })

        status.displayData = com_productList.value

        watch(status.displayData,() => { 
            let rawList = toRaw(status.displayData)
            let tmp = []
            for(let item of rawList){
                let arr = []    
                for(let secItem of item.item){
                    if(secItem.choosed){
                        arr.push(secItem)

                    }
                }
                if(arr.length!==0){
                    item.item = arr
                    tmp.push(item)
                }
            }

            console.log('1111');
            rea_choosed_list.value = tmp
         })

 

    

        //
       

        //

        //
         function getNum($event,item,index,secItem,secIndex){
              //inputinput
              status.productList[index].item[secIndex].num = $event
         }

        //=
        function itemChoose($event,skuId,secItem){
            //
            let e = $event||window.event
            e.target.parentElement.parentElement.classList.toggle('item-choosed')
            let judge = /choosed/.test(e.target.parentElement.parentElement.className)
            //
            //  addChoosed(skuId,judge)//judge
            judge?secItem.choosed=true:secItem.choosed =false
            
        }
        //skuidproduct Listchoosed,
        // function addChoosed(skuId,judge){
        //     let id = skuId
        //     for(let item of status.displayData){
        //         for(let secItem of item.item){
        //             console.log(secItem[0].skuId,id);
        //             if(secItem[0].skuId == id){
        //                 console.log(secItem[0].skuId,id);
        //                 if(judge){
        //                     secItem.choosed = true

        //                 }else{
        //                     secItem.choosed = false
                        
        //                 }
                        
        //             }
        //         }
        //     }

        // }

        //
        function shopChoose($event,shopId){
            let e = $event||window.event
            e.target.classList.toggle('all-choosed')
            let judge = /choosed/.test(e.target.className)
            let el = document.getElementById(shopId)
            let el_list = Array.from(el.children)
            if(el_list.length!==0){
                if(judge){
                    el_list.forEach((item,index,arr) => { 
                        item.classList.add('item-choosed')
                        item.firstElementChild.firstElementChild.checked = true
                        console.log(item.firstElementChild );
                     })

                }else{
                    el_list.forEach((item,index,arr) => { 
                        item.classList.remove('item-choosed')
                        item.firstElementChild.firstElementChild.checked = false
                     })
                    
                }
            }
            //
            status.displayData.forEach((item,index,arr) => { 
                if(item.shopId ==shopId){     
                        for(let value of item.item){
                            judge?value.choosed = true:value.choosed = false
                        }  
                } 
             })
          
        }
        //
        function allItemChoose($event){
            let e = $event||window.event
            e.target.classList.toggle('all-shop-choosed')
            let judge = /choosed/.test(e.target.className)
            
            if(judge){
                
               let list =  document.querySelectorAll("input[type='checkbox']")
               for(let item of list){
                item.checked = true
                } 
                
            }else{
                let list =  document.querySelectorAll("input[type='checkbox']")
                for(let item of list){
                item.checked = false
                }

            }
            //
            status.displayData.forEach((item,index,arr)=>{
                for(let secItem of item.item){
                    judge?secItem.choosed = true:secItem.choosed = false

                }
            })

            console.log(status.displayData,isProxy(status.displayData));
            
            //
            let el_shop = toRaw(nodeShop.value)
            el_shop.forEach((el) => { el.classList.toggle('all-choosed') })

            let el_Item = toRaw(nodeItem.value)
            el_Item.forEach((el) => { el.classList.toggle('item-choosed') })
        }
        //
        function deleteItem($event,item,secIndex){
         
            item.item.splice(secIndex,1)

        }
        
        
        
        return{
            rea_choosed_list,
            status,
            math,
            nodeShop,
            nodeItem,
            getNum,
            itemChoose,
            shopChoose,
            allItemChoose,
            deleteItem,


        }
    }

}
</script>

<template>
    <div class="car-outside-wrap">
    <div class="car-wrap">
        <div class="car-header">
            <div class="carCount">
                <em></em>
                <span class="span-carCount">57</span>

            </div>
            <div class="address">
                <em></em>
                <Address></Address>
            </div>

        </div>
        <div class="container-wrap">
            <div class="tittle-wrap">
                <div class="tittle">
                    <div class="colum all-check">
                        <input type="checkbox" name="all-checkbox" id="tittle-checkbox" @click="allItemChoose($event)">
                        <label for="tittle-checkbox"></label>
                    </div>
                    <div class="colum product">
                        <em></em>
                    </div>
                    <div class="colum single-price">
                        <em></em>
                    </div>
                    <div class="colum count">
                        <em></em>
                    </div>
                    <div class="colum summary">
                        <em></em>
                    </div>
                    <div class="colum action">
                        <em></em>
                    </div>
                </div>
            </div>
            <div class="list-wrap">
                <div class="list">
                    <ul class="ul-list" v-if="status.displayData">
                        <li class="item" v-for="(item,index) in status.displayData">
                            <div class="product-wrap">
                                <div class="shop-wrap">
                                    <div class="shop" :data-shopId="item.shopId" ref="nodeShop">
                                        <input type="checkbox" name="shop-checkbox"  @click="shopChoose($event,item.shopId)" :data-shopId="item.shopId">
                                        <a :href="item.shopUrl"> {{item.shopName}}</a>
                                    </div>
                                    <div class="attention">
                                        <a href="javascript:void(0);">
                                            <i></i>
                                            <span>PLUS5></span>
                                        </a>
                                    </div>
                                </div>
                                <div class="promote-wrap">
                                    <div class="promote">
                                        <div>
                                            <i></i>
                                            <span>2005030070</span>
                                        </div>

                                    </div>
                                </div>
                                <div class="product-list-wrap">
                                    <div class="product-list">
                                        <ul :id="item.shopId">
                                            <li class="product-item" v-for="(secItem,secIndex) in item.item" :data-skuId="secItem[0].skuId" ref="nodeItem" >
                                                <div class="column item-check">
                                                    <input type="checkbox" name="item-checkbox" id="id-item-checkbox" @click="itemChoose($event,secItem[0].skuId,secItem)" :data-skuId="secItem[0].skuId">
                                                </div>
                                                <div class="column item-img">
                                                    <img :src="item.imageurl" alt="">
                                                </div>
                                                <div class="column item-wname">
                                                    {{ secItem['wname']}}
                                                </div>
                                                <div class="column item-colorSize">
                                                    {{ secItem[0][''] }}
                                                </div>
                                                <div class="column item-single-price">{{ secItem.price }}</div>
                                                <div class="column item-count" :id="secItem[0].skuId">
                                                    <ElInputNumber  :min="1" :model-value="secItem.num"  @input="getNum($event,item,index,secItem,secIndex)"></ElInputNumber>
                                                    
                                                </div>
 
                                                <div class="column item-summary">
                                                     {{ math.multiply(secItem.price,secItem.num) }} 
                                                 <em></em>
                                                </div>
                                                <div class="column item-action">
                                                    <span class="item-delete" :data-skuId="secItem[0].skuId" @click="deleteItem($event,item,secIndex)"></span>
                                                    <span class="item-addFocus" :data-skuId="secItem[0].skuId"></span>
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="summary-wrap">
                <div class="summary">
                    <div class="summary-count"></div>
                    <div class="summary-price"></div>
                    <div class="sumary-submit">
                        <SubmitButton label="" action="{'name':'router_push','path':'/pay'}" :data="rea_choosed_list"></SubmitButton>
                    </div>
                    <div >{{ rea_choosed_list }}</div>
                </div>
        </div>
        
    </div>
</div>


</template>

<style lang="scss" scoped>
@use '@/scss/index.scss' as init;

$pageRed :#e2231a;

 .car-outside-wrap{
    background-color: white;
}
.car-wrap{
    margin: 0 auto;
    background-color: white;
   
    width: 900px;
    height: 900px;
    .car-header{
        // height: 50px;
       
        .carCount{
           
            height: 50px;
            font-size: 16px;
            color: $pageRed;
            font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
            font-weight: bold;
            text-align: center;
            line-height: 50px;
            float: left;
          
        }
        .address{
            float: right;
            height: 50px;
            font-size: 14px;
           

            em{
                line-height: 40px;
            }
           

            .address-wrap{
              box-sizing: border-box;
              float: right;
              margin: 10px 0; 
            } 
        }

        &::after{
            @include init.clearfix()
        }
    }
    .container-wrap{
       
        .tittle-wrap{
            height: 50px;
            background-color: rgb(245, 243, 243);
            .tittle{
                font-size: 16px;
                height: 30px;
                padding-top: 10px;
                padding-bottom: 10px;
                .colum{
                    float: left;
                }
                .all-check{
                    width: 200px;
                    
                    input[type="checkbox"]{
                        width: 16px;
                        height: 16px;
                    }

                }
                .product{
                    width: 350px;

                }
                .single-price{
                    width: 100px;
                }
                .count{
                    width: 100px;
                }
                .summary{
                    width: 100px;
                }

            }
        }
        .list-wrap{
     
            ul.ul-list{
                li.item{
                    margin-bottom: 10px;
                    display: block;
              
                    background-color: #fff;
                }
            }
        }



    }
    .summary-wrap{
            .summary{
                display: flex;
                .summary-count{
                 
                    flex: 1 1 auto;

                }
                .summary-price{
                    @extend .summary-count;
                    width: 200px;
                    
                }
                .summary-submit{
                    @extend .summary-price

                }

            }

        }
    .product-wrap{
        .shop-wrap{
            height: 30px;
            border-bottom: solid 2px #c5c5c5;

            .shop{
                float: left;

                line-height: 30px;
                font-size: 14px;
                font-weight: bold;
                
                input[type='checkbox']{
                    width: 14px;
                    height: 14px;
                }
            }
            .attention{
                float: right;
                height: 30px;
                a{
                    line-height: 30px;
                    font-size: 12px;
                    i{
                        background-color: red;
                    }
                }
                
            }
    
        }
        .promote-wrap{
            height: 40px;
           
       
           
        }
        .product-list-wrap{
            .product-list{
                background-color: brown;
             
                ul{
                    li{
                        display: block;
                        background-color:#fff;
                        height: 90px;
                        border: solid 1px #c5c5c5;
                        font-size: 12px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        &.item-choosed{
                            background-color:#fff4e8;
                        }
                        .column{
                            flex: 1 1 auto;
                            margin-left: 5px;
                    
                        }
                        .item-check{
                            width: 20px;
                            margin-left: 5px;
                            input[type='checkbox']{
                                width: 16px;
                                height: 16px;
                            }
                        }
                        .item-img{
                            width: 70px;   
                            display: block;
                            margin-left: 5px;
                            
                            img{    
                                width: 70px;     
                                display: block;
                                flex: 0.8 1 auto;
                            }
               
                        }
                        .item-wname{
                            width: 250px;
                        }
                        .item-colorSize{
                            width: 150px;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                        }
                        .item-count{
                            width: 100px;
                            .el-input-number{
                               
                                width: 100px;
                            }
                        }
                        .item-single-price{
                            width: 70px;
                        }
                        .item-summary{
                            width: 70px;
                        }
                        .item-action{
                            span{
                                display: block;
                                margin-bottom: 5px;
                            }
                        }
                        &::after{
                            @include init.clearfix();
                        }
                    }
                }
            }
        }
        &::after{
            @include init.clearfix();
        }
    }
}
</style>