<template>
    <div class="submitButton">
        <a class="car" @click="fn_click($event)">
            {{ props.label }}

        </a>
    </div>
</template>
<script >
import store from '@/store';

import { onRenderTracked, reactive } from 'vue';
import { useRouter } from 'vue-router';



 export default {
    'name':'submitButton',
     props:['label','action','data'],
     setup(props,context){
        const router = useRouter()


         function fn_click($event){
            let params = props.data
            if(params instanceof Array&&params.value.length!==0){
                store.dispatch('saveReqPayFor',params)
                router.push({path:'/payFor'})
            }

         }

        
        return {
            props,
            fn_click
        }

     }


 }
</script>


<style lang="scss" scoped>



.car{
        border: solid 1px #ccc;
        background-color:red;
        line-height: 40px;
        width: 100px;
        font-size:16px;
        padding-left: 10px;
        color:white;
        float: left;
        margin-left: 20px;
        position: relative;
        i{  
            display: none;
            position: absolute;
            right: -30px;
            top:0px;
            width: 30px;
            height: 30px;
            color: green;
            font-size: 30px;
            animation-duration: 1s;
           &.animate__fadeInUp{
            display: block;
           }
        }
        span{
            display: none;
            position: absolute;
            left: 120px;
            top:0px;
            width: 130px;
            height: 30px;
            color: red;
            font-size: 16px;
            text-decoration: underline;

        }

      }

</style>